<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Login form</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field
                label="Login"
                name="login"
                prepend-icon="mdi-account"
                type="text"
                v-model="input.username"
              ></v-text-field>
              <v-text-field
                id="password"
                label="Password"
                name="password"
                prepend-icon="mdi-lock"
                type="password"
                v-model="input.password"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="login">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar :value="notif" :timeout="timeout">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="notif = false">Close</v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import axios from 'axios';
import qs from "qs";
import helpers from '../helper/mainhelper.js';

export default {
  name: "Login",
  data: () => ({
    input: {},
    notif: false,
    text: "Wrong Password",
    timeout: 5000,
  }),
  methods: {
    login() {
      let url = helpers.baseurl();
      axios.post(`${url}/auth/auth_process`, qs.stringify({
        username: this.input.username,
        password: this.input.password
      })).then((result) => {
        let res = result.data;
        if (res.result == "success") {
          localStorage.usertoken = res.token;
          localStorage.username = res.user;
          this.$router.push({ name: 'Home' })
        } else {
          this.notif = true;
          console.log("show up pop up wrong password")
        }
      });
    },
  },
};
</script>

